import isBrowser from "../isBrowser";
import { MsgType } from "./types";
if (isBrowser()) {
  var webWorker = self;
  webWorker.addEventListener('message', function (message) {
    var data = message.data;
    switch (data.type) {
      case MsgType.Create:
        {
          var $timer = setTimeout(function () {
            webWorker.postMessage({
              taskId: data.taskId,
              type: MsgType.Execute
            });
            clearTimeout($timer);
          }, data.wait);
          webWorker.postMessage({
            taskId: data.taskId,
            timer: $timer,
            type: MsgType.Create
          });
          break;
        }
      case MsgType.Clear:
        {
          clearTimeout(data.timerId);
          webWorker.postMessage({
            taskId: data.taskId,
            type: MsgType.Clear
          });
          break;
        }
    }
  });
}
export default null;